import React, { useState } from 'react';
import './style.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCheckCircle,
  faCircle,
  faTimesCircle,
  faTrashAlt,
} from '@fortawesome/free-solid-svg-icons';

export const TrackingItem = ({item}) => {
  const [isEdit, updateIsEdit] = useState(false);
  const [isChecked, updateIsChecked] = useState(false);

  return (
    <div className={`tracking-item-container theme-dark ${isChecked ? 'color-checked' : 'color-unchecked'}`}>
      <div className='tracking-item' onClick={() => updateIsEdit(!isEdit)}>
        <div>{item.name}</div>
        <FontAwesomeIcon icon={item.isChecked ? faCheckCircle : faCircle} />
      </div>
      <div className={`tracking-item-menu ${!isEdit ? 'hidden-menu' : ''}`}>
        <div className={`tracking-item-menu-item ${!isEdit ? 'hidden-menu-item' : 'visible-menu-item'}`}>
          <FontAwesomeIcon icon={faTimesCircle}  />
        </div>
        <div className={`tracking-item-menu-item ${!isEdit ? 'hidden-menu-item' : 'visible-menu-item'}`}>
          <FontAwesomeIcon icon={faTrashAlt}  />
        </div>
      </div>
    </div>
  );
};



