import React from 'react';
import './style.scss';
import { TrackingItem } from '../TrackingItem';

export const TrackingList = ({ items }) => {
  return (
    <div className='tracking-list'>
      {items.map(item => (
        <TrackingItem item={item} key={item.name} />
      ))}
    </div>
  );
};

