import { useState, useReducer } from 'react';
import { Header } from './components/Header/';
import './App.scss';
import { ItemForm } from './components/ItemForm';
import { TrackingList } from './components/TrackingList';
import { Menu } from './components/Menu';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faSyncAlt,
  faPlusCircle,
  faEllipsisH,
  faTimes,
  faPlus,
  faInfo,
} from '@fortawesome/free-solid-svg-icons';

function App() {
  const initialItem = {
    name: 'Take vitamins',
    cycle: {
      type: 'DAILY',
      count: 1,
    },
    isOnEdit: false,
    isChecked: false,
    checkingTimestamp: 0,
  };
  const initialItem2 = {
    name: 'do somthing',
    cycle: {
      type: 'DAILY',
      count: 1,
    },
    isOnEdit: false,
    isChecked: true,
    checkingTimestamp: 0,
  };
  

  const [isCreatingItem, updateIsCreatingItem] = useState(false);
  const [menu, setMenu] = useReducer(
    (state, newState) => ({ ...state, ...newState }),
    { isOpeningMenu: false, isMenuOpen: false, isMenuSlide: false }
  );
  const [items, updateItems] = useState([initialItem, initialItem2]);


  const handleCreatingItem = boolean => {
    updateIsCreatingItem(boolean);
    handleCloseMenu();
  };

  const handleMenuClick = () => {
    if (menu.isMenuOpen) handleCloseMenu();
    else handleOpenMenu();
  };

  const handleOpenMenu = () => {
    setMenu({ isOpeningMenu: true, isMenuOpen: true });
    setTimeout(() => {
      setMenu({ isMenuSlide: true });
    }, 200);
  };

  const handleCloseMenu = () => {
    setMenu({ isMenuSlide: false });
    setTimeout(() => {
      setMenu({ isOpeningMenu: false, isMenuOpen: false });
    }, 200);
  };

  return (
    <div className='App'>
      <Header />
      {isCreatingItem && (
        <div className='item-form-container'>
          <ItemForm />
        </div>
      )}
      {!isCreatingItem && !!items.length && (
        <main className='main'>
          <TrackingList items={items} />
        </main>
      )}
      {!isCreatingItem && !items.length && (
        <div className='initial-create-container'>
          <div
            className='initial-create-button'
            onClick={() => handleCreatingItem(true)}
          >
            <FontAwesomeIcon icon={faPlusCircle} size='4x' />
            <div>Create your first item</div>
          </div>
        </div>
      )}
      <Menu
        menu={menu}
        handleMenuClick={handleMenuClick}
        onCreatingItem={handleCreatingItem}
        isCreatingItem={isCreatingItem}
      />
    </div>
  );
}

export default App;
