import React, { useState } from 'react';
import './style.scss';
import { faCheck, faPlus } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const ItemForm = () => {

  const [textInput, updateTextInput] = useState('');

  const handleInput = (ev) => {
    ev.persist();
    updateTextInput(ev.target.value);
  }

  const handleSubmit = (ev) => {
    ev.preventDefault();
    if (textInput.trim()) {
      alert(textInput.trim());
    }
  }

  return (
    <form className='item-form' onSubmit={handleSubmit} >
      <div className='item-label'>Item name:</div>
      <input className='item-input' type='text' value={textInput} onChange={handleInput} />
      <button className='item-submit'><FontAwesomeIcon icon={faCheck} /></button>
    </form>
  );
};
